.cshp-pt-warning {
    display: none;
}

.datatable-pagination-list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.datatable-pagination-list-item a {
    color: #ffffff;
    background-color: #2271b1;
    color: #ffffff;
    padding: 5px 8px;
    cursor: pointer;
}

.datatable-pagination-list-item.datatable-active a {
    background-color: #2c3338;
    cursor: text;
}

.cshp-table-border {
    border: 1px;
}

#cshp-wp-cli-plugin-install-command {
    width: 100%;
    height: 13rem;
}

#cshp-wp-cli-theme-install-command {
    width: 100%;
    height: 6rem;
}

/* hide the activate plugin button after installing the premium plugins from the Cornershop plugin recovery site since we cannot activate the premium plugins backup plugin on the install screen*/
.plugin-card[class*="plugin-card-cpr-cornershop-plugin_recovery"] .activate-now {
    display: none;
}